.App {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: 'Open Sans', sans-serif;
    padding-top: 10px;
    color: #333;
}

body {
    background-color: #fcfcfc;
}

.container {
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 8px;
    background-color: #d5c6b9;
    border-radius: 8px;
    width: 450px;
}

.piece {
    background-color: #f4ede6;
    height: 70px;
    width: 140px;
    cursor: pointer;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    user-select: none;
}

.piece.correct {
    background-color: #3498db;
    color: white;
}

.piece.incorrect {
    color: white;

    background-color: #e26a6a;
}

.clueText {
    margin-bottom: 30px;
    user-select: none;
    background-color: #5a5e5e;
    font-size: 20px;
    display: flex;
    text-align: center;
    padding: 10px 20px;
    border-radius: 4px;
    align-items: center;
    justify-content: center;
    color: white;
}

.clue {
    font-weight: 800;
    margin-left: 15px;
}

.helpText {
    margin-bottom: 30px;
    font-size: 14px;
}

.title {
    margin-bottom: 10px;
    font-weight: 900;
    font-size: 30;
}

.nextBoardSection {
    border-top: 1px solid #ddd;
    padding-top: 20px;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nextBoardButton {
    width: 100px;
    margin-top: 6px;
    padding: 8px 16px;
    border-radius: 4px;
    user-select: none;
    margin-bottom: 10px;
    cursor: pointer;
    background-color: #eee;
}
.disclaimer {
    margin-top: 10px;
    font-size: 13px;
    max-width: 600px;
}
